.products-table {
    width: 100%;
    margin-top: 50px; // Ajusta el espacio superior según sea necesario
    table {
      width: 100%;
      border-collapse: collapse; // Elimina el espacio entre bordes
      table-layout: fixed; // Ayuda a manejar la anchura de las columnas
  
      th, td {
        text-align: left;
        padding: 8px;
        border-bottom: 1px solid #ddd; // Línea divisoria entre filas
  
        &:last-child {
          text-align: right;
        }
      }
  
      th {
        background-color: #484c4c; // Color de fondo para los encabezados
        color: white;
      }
      th:nth-child(n+2), // selecciona todas las th a partir del segundo
      td:nth-child(n+2) { // selecciona todas las td a partir del segundo
        text-align: left; // alinea el texto a la izquierda
        padding-left: 20px; // añade más espacio a la izquierda si es necesario
      }
  
      .details-btn,
      .movements-btn {
        padding: 5px 10px;
        border: none;
        border-radius: 4px;
        cursor: pointer;
  
        // Asumiendo que quieres un estilo similar al botón de "Añadir mercadería"
        background-color: #009688; // Verde azulado para "Ver detalle"
        color: white;
  
        &:hover {
          background-color: darken(#009688, 10%); // Oscurecer al pasar el mouse
        }
      }
  
      // Estilos adicionales para .movements-btn si es necesario
      .movements-btn {
        background-color: #009688; // Un tono diferente para "Ver movimientos"
        &:hover {
          background-color: darken(#009688, 10%);
        }
      }
    }
  }
  