.products-container {
    padding: 20px;

    .products-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
        margin-top: 10px;
        h1 {
            margin: 0;
        }

        .add-merchandise-btn {
            padding: 10px 15px;
            background-color: #009688; // Un verde azulado similar al del diseño
            color: #fff;
            border: none;
            border-radius: 5px;
            font-size: 16px;
            cursor: pointer;
            transition: background-color 0.3s;
    
            &:hover {
                background-color: darken(#009688, 10%); // Oscurece el botón un poco al pasar el mouse
            }
        }
    }
    .products-filters {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        gap: 90px; // Esto crea espacio entre los elementos de filtro
        padding: 10px;
        margin-top: 40px;
    
        .filter-item {
            display: flex;
            align-items: center;
            
            label {
                margin-right: 5px;
            }
            select,
            input[type="text"] {
              padding: 12px; // Aumenta el padding para hacer el elemento más alto
              border-radius: 4px; // Añade un redondeo suave a las esquinas
              border: 1px solid #ccc; // Añade un borde ligero, ajusta el color según sea necesario
            }

    
            input[type="text"] {
                min-width: 200px; 
            }
    

        }
    
        .search-btn {
            padding: 5px 15px;
            background-color: #009688;
            color: white;
            border: none;
            border-radius: 4px;
            cursor: pointer;
    
            &:hover {
                background-color: darken(#009688, 10%);
            }
        }
    }
    .products-pagination-and-back {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 20px; // Ajusta esto según sea necesario
    
        .back-btn {
            padding: 10px 40px; // Aumenta el padding horizontal para hacer el botón más ancho
            background-color: #f2f2f2; // Ajusta el color de fondo según tus preferencias
            border: 2px solid #009688; // Aumenta el grosor del borde y cambia el color
            color: #009688; // Cambia el color del texto al mismo del borde
            border-radius: 4px;
            font-weight: bold; // Opcional: si quieres que la fuente sea más gruesa
            cursor: pointer;
            transition: background-color 0.3s, color 0.3s; // Suaviza la transición de colores
            
            &:hover {
                background-color: #009688; // Cambia el color de fondo al mismo del borde
                color: #ffffff; // Cambia el color del texto a blanco para contraste
            }
        }
    }
    
    
}
    