.Navbar {
    width: 70px; // Ancho inicial reducido
    height: 100vh; // Para asegurar que ocupe toda la altura
    background-color: #158D8D; // El color de fondo de tu imagen
    transition: width 0.3s ease; // Suaviza la transición del ancho
    position: relative; // Posicionamiento relativo para el Navbar
    z-index: 2; // Asegúrate de que el Navbar esté sobre otros elementos si es necesario
  
    // Estilo para el ícono de menú
    .Navbar__toggle {
        position: absolute;
        top: 10px; // Mantén el ícono en la parte superior
        left: 50%; // Coloca el punto de partida del ícono en el centro
        transform: translateX(-50%); // Desplaza el ícono a la izquierda la mitad de su ancho para centrarlo
        z-index: 3; // Asegura que el ícono quede sobre los otros elementos del Navbar
        cursor: pointer;
      color: white; // Color del ícono
  
      span {
        font-size: 1.5rem; // Ajusta esto según el tamaño del ícono que desees
      }
    }
  
    // Estilos para los elementos de la lista
    .NavBar__sidebarList {
      list-style-type: none;
      margin: 0;
      padding: 2rem 0 0; // Espacio en la parte superior
      opacity: 0; // Inicia invisible
      transition: opacity 0.3s ease; // Transición suave para la opacidad
  
      .NavBar__sidebarList-dataRow {
        text-decoration: none;
        color: white; // Color del texto
        padding: 1rem;
        display: block;
        white-space: nowrap; // Evita que los textos se ajusten al cambiar el tamaño
  
        &:hover,
        &.selected {
          background-color: #20cece; // Un color más claro para el hover y selección
        }
      }
    }
  
    // Cuando el Navbar está expandido
    &--expanded {
      width: 200px; // El ancho del Navbar cuando está expandido
  
      .NavBar__sidebarList {
        opacity: 1; // Hace los elementos visibles
      }
    }
  }
  