.layout-basic {
    display: flex; // Usa flexbox para organizar el `Navbar` y el contenido principal en una fila

    
    .layout-basic__content {
      flex-grow: 1; // Esto hace que el contenido principal ocupe el espacio restante
      margin-left: 20px; // Ajusta el margen izquierdo para crear espacio entre el `Navbar` y el contenido
      padding: 20px; // Añade un poco de padding alrededor del contenido
      background-color: #f7f7f7; // Este es el color de fondo de tu área de contenido
      // Agrega cualquier otro estilo que necesites para el contenido principal
    }
  }
  