.products-container2 {
    padding: 20px;

    .products-header2 {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
        margin-top: 10px;
        h1 {
            margin: 0;
        }

        .add-merchandise-btn2 {
            padding: 10px 15px;
            background-color: #009688; // Un verde azulado similar al del diseño
            color: #fff;
            border: none;
            border-radius: 5px;
            font-size: 16px;
            cursor: pointer;
            transition: background-color 0.3s;
    
            &:hover {
                background-color: darken(#009688, 10%); // Oscurece el botón un poco al pasar el mouse
            }
        }
    }
    .products-filters2 {
        display: flex;
        align-items: center; // Asegúrate de que todos los hijos se alineen verticalmente en el centro
        justify-content: space-between;
        margin-bottom: 20px; // Espaciado debajo del contenedor de filtros
    
        h2 {
            margin: 0; // Remueve márgenes por defecto si son necesarios
        }
    
        .filter-item {
            display: flex;
            flex-direction: column;
            align-items: flex-start; // Alinea los labels y los inputs/selects
    
            label {
                margin-bottom: 5px;
            }
    
            input[type="date"],
            select {
                padding: 5px;
                border: 1px solid #ccc;
                border-radius: 4px;
            }
        }
    
        .search-btn {
            padding: 10px 20px;
            background-color: #009688;
            border: none;
            border-radius: 4px;
            color: white;
            cursor: pointer;
            &:hover {
                background-color: darken(#009688, 10%);
            }
        }
    }
    
    .products-pagination-and-back2 {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 20px; // Ajusta esto según sea necesario
    
        .back-btn {
            padding: 10px 40px; // Aumenta el padding horizontal para hacer el botón más ancho
            background-color: #f2f2f2; // Ajusta el color de fondo según tus preferencias
            border: 2px solid #009688; // Aumenta el grosor del borde y cambia el color
            color: #009688; // Cambia el color del texto al mismo del borde
            border-radius: 4px;
            font-weight: bold; // Opcional: si quieres que la fuente sea más gruesa
            cursor: pointer;
            transition: background-color 0.3s, color 0.3s; // Suaviza la transición de colores
            
            &:hover {
                background-color: #009688; // Cambia el color de fondo al mismo del borde
                color: #ffffff; // Cambia el color del texto a blanco para contraste
            }
        }
    }
    
    
}
    